.navigation {
  -ms-flex-align: center;
      align-items: center;
  display: -ms-flexbox;
  display: flex;
  height: 4vh;
  -ms-flex-pack: center;
      justify-content: center;
  width: 100vw;
}

nav {
  background: -webkit-gradient(linear, left top, left bottom, from(rgba(0, 0, 0, 0.4)), to(rgba(0, 0, 0, 0.0)));
  background: -webkit-linear-gradient(rgba(0, 0, 0, 0.4), rgba(0, 0, 0, 0.0));
  background: -o-linear-gradient(rgba(0, 0, 0, 0.4), rgba(0, 0, 0, 0.0));
  background: linear-gradient(rgba(0, 0, 0, 0.4), rgba(0, 0, 0, 0.0)); 
  -webkit-box-shadow: none; 
          box-shadow: none;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
}

nav a {
  color: rgba(255, 255, 255, 0.7);
  font-family: 'Work Sans', sans-serif;
  font-weight: lighter;
  font-size: 1em;
  margin: 0 2em;
  text-transform: uppercase;
  -webkit-transition: 0.5s;
  -o-transition: 0.5s;
  transition: 0.5s;
}

nav a:hover {
  color: black;
  -webkit-transition: 0.5s;
  -o-transition: 0.5s;
  transition: 0.5s;
}



